 <template>
    <b-container>
        <div class="boxCam">
          <b-row>
              <b-col>
                  <div v-if="this.cam == true">
                <img class="image" :src="teste">
                    </div>
                    <div v-else class="camOff">
                        <p id="textCamOff">Cam Off</p>
                    </div>
                </b-col>
              <b-col>
                    <b-button-group vertical class="buttons">
                        <b-button @click="power()">
                            <img class="power" src="../assets/power.svg">            
                        </b-button>
                        <b-button>Middle</b-button>
                        <b-button>Bottom</b-button>
                    </b-button-group>
              </b-col>
          </b-row>
         </div>
    </b-container>
</template>

<script> 
        const axios = require('axios');

export default {
    name: "boxCam",
    data(){
        return{
            cam: false,
            teste: "http://192.168.1.206:5000/video_feed"
          
    }
    },
    methods:{
    power(){
        var string
        if(this.cam == true){
             string = "Cam/Off"
            this.cam = false 
        }else{
            string = "Cam/On"
            this.cam = true
        }
         axios.get('http://192.168.1.206:5000/' + string)
        .then(function(response){
            console.log(response.data); // ex.: { user: 'Your User'}
            console.log(response.status); // ex.: 200
        });
    }
    }
}
</script>

