    margin-left: -10px;
}
.image{
    width:280px;
    margin-top:20px;
    margin-left:10px;
    border: solid 2px rgb(77, 75, 75);
}
.camOff{
    width:280px;
    height:250px;
    background-color: grey;
    margin-top:20px;
    margin-left:10px;
    text-align: center;
    border: solid 2px rgb(77, 75, 75);
}
#textCamOff{
    position:relative;
    top:40%;
    font-size:25px;
    color: rgb(77, 75, 75);
}
