<template>
  <div id="app">
  <b-container fluid>
    <div class='header'>
      <b-row>
        <b-col>
            <p id="line"></p>
        </b-col>
          <b-col md="3" sm="5">
            <p id="hora">{{hora}}</p>
          </b-col>
        <b-col>
            <p id="line"></p>
        </b-col>
      </b-row>
    </div>
      <b-row>
        <b-col md="4" sm="12" >
          <box/>
        </b-col>
        <b-col md="4" sm="12">
          <boxLuz :statusQuarto="this.Quarto.Status" :statusCloset="this.Closet.Status" :statusBanheiro="this.Banheiro.Status" :statusLuminaria="this.Quarto.Luminaria"/>
        </b-col>
        <b-col md="4" sm="12">
          <boxCpu :valorMhz="this.Servidor.Cpu.Atual" :Min="this.Servidor.Cpu.Min" :Max="this.Servidor.Cpu.Max" :valorPercent="this.Servidor.Cpu.Porcentagem" :valorMemoria="this.Servidor.Memoria.Porcentagem"/>
        </b-col>
      </b-row>
       <b-row>
        <b-col md="4" sm="12">
          <boxColorPicker/>
        </b-col>
        <b-col md="4" sm="12">
          <boxControle />
        </b-col>
        <b-col md="4" sm="12">
          <boxCam />
        </b-col>
      </b-row>
</b-container>

  </div>

</template>

<script>
import boxLuz from "./main/boxLuz.vue";
import box from "./main/box.vue";
import boxCam from "./main/boxCam.vue";
import boxControle from "./main/boxControle.vue";


// import boxTemp from "./main/boxTemp.vue";
import boxCpu from "./main/boxCpu.vue";
import boxColorPicker from "./main/boxColorPicker.vue";
const axios = require('axios');

export default {
  name: 'App',
  data(){
    return{
      isConnected: false,
      Quarto:{
              Temp: null, 
              Status: null,
              Tranca: null,
              Luminaria: null,
              Camera:{
                  Ligada :null,
                  Gravar : null
              }
      },
      Closet:{
              Status: null
      },
      Banheiro:{
            Status: null
      },
      Servidor:{
          Cpu:{
              Porcentagem: null,
              Atual: null,
              Min: null,
              Max: null
          },
      Memoria :{
          Total : null,
          Usado : null,
          Porcentagem : null
      }
      },
      hora: null
    }
    
  },
  components:{
    boxLuz,
    box,
    // boxTemp,
    boxCpu,
    boxCam,
    boxColorPicker,
    boxControle
  },
created () {
	setInterval(() => {
         axios.get('http://192.168.1.206:5000/Status')
        .then(response => {
          this.Servidor.Cpu.Porcentagem = response.data.Servidor.Cpu.Porcentagem
          this.Servidor.Cpu.Atual = response.data.Servidor.Cpu.Atual;
          this.Servidor.Cpu.Min = response.data.Servidor.Cpu.Min;
          this.Servidor.Cpu.Max = response.data.Servidor.Cpu.Max;
          this.Servidor.Memoria.Porcentagem = response.data.Servidor.Memoria.Porcentagem;
          this.Quarto.Status = response.data.Quarto.Status ? "green" : "red"
          this.Closet.Status = response.data.Closet.Status ? "green" : "red"
          this.Banheiro.Status = response.data.Banheiro.Status ? "green" : "red"
          this.Quarto.Luminaria = response.data.Quarto.Luminaria ? "green" : "red"
          })
          var data = new Date()
          this.hora = data.getHours() + ":" + data.getMinutes() + ":" + data.getSeconds()

  }, 1000)
}
}

</script>

<style>
#app{
  margin: 0px;
  padding: 0px;
}
.header{
  height: 150px;
  padding: 0px;

}

#line{
  width: auto;
  height:3px;
  background-color: blue;
  margin-top:45px;
}
@media only screen and (max-width: 600px){
  #line{
  width: 410px;
  height:3px;
  background-color: blue;
  margin-top:0px;
  }
}
#hora{
  font-size: 60px;
  text-align: center;

}
</style>


