<template>
    <div>
        <p :style="{backgroundColor: backgroundColor}" id="status"></p>
        <b-button id="button" type="button" @click="run()">{{comodo}}</b-button>
    </div>
</template>
<script>
const axios = require('axios');
export default {
    props: {
        comodo: null,
        backgroundColor: null,
        url: null
    },
    data() {
        return {
        }
    },  
    methods:{
        run() {
            var string = ''
            if(this.url == null){
                string = 'http://192.168.1.206:5000/' + this.comodo
            }else{
                string = 'http://192.168.1.206:5000/' + this.url
            }
            axios.get(string)
            .then(function(response){
                console.log(response.status); // ex.: 200
            }); 
        }
 }  
}
</script>
