<template>
    <b-container>
        <div class="boxColorPickerContanier">
            <div v-bind:style="styleObject" class="boxColorPicker"></div>
                <color-picker v-bind:style="picker" id="colorPicker" @select="onColorSelect"  @input="onColorInput" v-model="color"></color-picker>
            </div>
    </b-container>
</template>

<script>
import ColorPicker from '@radial-color-picker/vue-color-picker';
const axios = require('axios');
export default {
    components: { ColorPicker },
    data() {
        return {
            color: {
                hue: 50,
                saturation: 100,
                luminosity: 50,
                isPressed: 1
            },
            pickerOn: false
        }
    },  
    methods: {
        onColorSelect(data) {
            console.log(data)

        },
        onColorInput(data) {
            console.log(data)
                axios.get('http://192.168.1.206:5000/Cor/' + data)
                    .then(function(response){
                        // console.log(response.data); // ex.: { user: 'Your User'}
                        console.log(response.status); // ex.: 200
                        this.pickerOn = true
                }); 
        }
    }
};
</script>

<style>
@import '~@radial-color-picker/vue-color-picker/dist/vue-color-picker.min.css';
.boxColorPickerContanier{
    height: 300px;
    width: 410px;   
    background-color:rgb(62, 62, 121);
    margin-top: 20px;
    border-radius: 10px;
    padding:0px;
    margin-left:-12px;
  

}
.boxColorPicker{
    background-color:rgb(37, 37, 39);
    border-radius: 10px;
    /* box-shadow: 10px 10px 5px grey; */


}
#colorPicker{
    margin: auto;
    position: relative;
    top:10px;

}

</style>